
















































































































































import FormApi from '@/apis/FormApi';
import { Form, FormModel } from '@/models/FormModel';
import Vue from 'vue';
import { parse } from 'json2csv';
import Utils from '@/libs/Utils';

export default Vue.extend({
  components: {
  },
  data() {
    return {
      searchString: '',
      checkMember: [],
      informMethodFilter: [],
      registeredFilter: [],
      informMethods: [
        'letter',
        'textmsg',
        'email',
        'line',
        'wechat',
      ],
    };
  },
  computed: {
    membersList(): FormModel[] {
      return this.$store.state.membersList;
    },
    trimSearchString(): string { // 修掉輸入的空白
      const name = this.searchString.trim();
      if (!name) {
        return name;
      }
      return name;
    },
    searchStringList(): FormModel[] {
      const searchResult = [] as FormModel[];
      this.membersList.forEach((groupsObject) => {
        let groupsName = '';
        let groupsPhone = '';
        groupsName = groupsObject.ceremonyForm.personalInformation.name;
        groupsPhone = groupsObject.ceremonyForm.personalInformation.phone;
        const inputString = this.trimSearchString;
        const indexOf = groupsName.toUpperCase().indexOf(inputString.toUpperCase());
        if (indexOf >= 0) {
          searchResult.push(groupsObject);
        }
        const indexOf2 = groupsPhone.toUpperCase().indexOf(inputString.toUpperCase());
        if (indexOf2 >= 0) {
          searchResult.push(groupsObject);
        }
      });
      return searchResult;
    },
    informMethodList(): FormModel[] {
      const filteredResult = [] as FormModel[];
      this.membersList.forEach((groupsObject) => {
        let groupsInformMethod = '';
        groupsInformMethod = groupsObject.ceremonyForm.personalInformation.informMethod;
        this.informMethodFilter.forEach((filterSting) => {
          if (groupsInformMethod === filterSting) {
            filteredResult.push(groupsObject);
          }
        });
      });
      return filteredResult;
    },
    registeredList(): FormModel[] {
      const filteredResult = [] as FormModel[];
      this.membersList.forEach((groupsObject) => {
        let groupsRegistered = '';
        groupsRegistered = String(groupsObject.isSubmitted);
        this.registeredFilter.forEach((filterSting) => {
          if (groupsRegistered === filterSting) {
            filteredResult.push(groupsObject);
          }
        });
      });
      return filteredResult;
    },
    filteredGroups(): FormModel[] {
      if (this.trimSearchString !== '') {
        return this.searchStringList;
      }
      if (this.informMethodFilter.length > 0) {
        return this.informMethodList;
      }
      if (this.registeredFilter.length > 0) {
        return this.registeredList;
      }
      return this.membersList;
    },
  },
  methods: {
    viewMemberForm(memberForm: FormModel) {
      this.$router.push('viewMemberForm');
      this.$store.commit('updateMemberForm', memberForm);
    },
    createNewMember() {
      this.$router.push('ceremonyFormsPage/textname');
      this.$store.commit('updateMemberForm', new Form());
      this.$store.commit('updateConfirmApplication', false);
      this.$store.commit('updateCheckedRepeatList', false);
    },

    deletePerson(index: number) {
      this.openDeleteModal(index);
    },
    openDeleteModal(index: number) {
      this.$modal.show('dialog', {
        title: '確定刪除嗎?',
        text: '刪掉就不會再回來了',
        buttons: [
          {
            title: '取消',
            handler: () => {
              this.$modal.hide('dialog');
            },
          },
          {
            title: '確定',
            handler: () => {
              this.$modal.hide('dialog');
              this.delete(index);
            },
          },
        ],
      });
    },
    async delete(index: number) {
      if (index >= 0 && index < this.membersList.length) {
        await FormApi.delete(this.membersList[index].formId);
        this.membersList.splice(index, 1);
      }
    },

    exportCsv() {
      const data = this.filteredGroups.map((form) => ({
        姓名: form.ceremonyForm.personalInformation.name,
        電話: form.ceremonyForm.personalInformation.phone,
        上次有無報名: form.isLastEventSubmitted,
        最新活動報名狀態: form.isSubmitted,
        通知方式: form.ceremonyForm.personalInformation.informMethod,
      }));

      const fields = ['姓名', '電話', '上次有無報名', '最新活動報名狀態', '通知方式'];
      const opts = { fields };

      try {
        const csv = parse(data, opts);
        console.log(csv);
        this.$notify({
          type: 'success',
          title: '已匯出會員名單',
        });
        Utils.exportCsv('會員名單.csv', csv);
      } catch (err) {
        console.error(err);
      }
    },
  },
});
